webix.protoUI({
	name:"altbutton",
	$cssName:"button",
	$renderInput:function(obj){
		var css = " class='webixtype_"+(obj.type||"base")+"' ";
		return "<button type='button' "+(obj.popup?"aria-haspopup='true'":"")+css+">"+webix.template.escape(obj.label||obj.value)+"</button>";
	}
}, webix.ui.button);